/* make variables */
:root {
  --lightgray: #D9D9D9;
  --gray: #9c9c9c;
  --orange: #f48915;
  --darkGrey: #464D53;
  --caloryCard: #656565;
  --planCard: linear-gradient(210.41deg, #fa5042 1.14%, #ffa739 100.75%);
  --appColor:  #3c3f45;
}
/* body{
  overflow-x: hidden;
} */
.App{
  background-image: url(data:image/png;base64,);
  mix-blend-mode: overlay;
  background-color: var(--appColor);
  display: flex;
  flex-direction: column;
  gap: 6rem;
  /* background-color: red; */
  overflow-x: hidden;
  /* overflow-x: hidden; */
}
::-webkit-scrollbar{
  display: none;
}
.stroke-text
{
  color:transparent;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-text-stroke-width:1px ;
  -webkit-text-stroke-color: white;
}
.btn
{
  width:8rem;
  padding:0.5rem;
  background-color: white;
  border:none;
  outline:none;
  font-weight:bold;
}
.btn:hover{
  cursor: pointer;
}
